import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, NgZone, ViewChild } from "@angular/core";

import { App, NavController, Platform, ViewController } from "ionic-angular";

import { AppConstants } from "@app/app.constants";
import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsAuthService } from "@services/auth.service";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsNavigationService } from "@services/navigation.service";
import { MarsPictureService } from "@services/picture.service";

import { Backend } from "@backend/index";
import { AppUserPages } from "@pages/user-pages";
import { Form, NgModel } from "@angular/forms";


@Component({
    selector: "page-pet-information",
    templateUrl: "pet-information.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class PetInformationPage {

    viewTitle: string = "";
    navigationService: MarsNavigationService;
    translations: AppTranslations;
    pet: any = { pictures: [], monthsAge: 1, mgWeight: 1 };
    tempers = ["temper_easy", "temper_medium", "temper_hard"];
    hair_lengths = ["short", "medium", "long"];
    sizes = ["P", "M", "G", "GG"];

    private fileInputProperties = {
        dropText: " ",
        browseText: "Selecionar",
        removeText: "Remove",
        invalidFileText: "You have picked an invalid or disallowed file.",
        invalidFileTimeout: 8000,
        removable: true,
        multiple: false,
        showPreviews: true,
        extensions: ["jpg"],
    };

    private changingPicture: boolean;

    constructor(private platform: Platform,
        private app: App,
        private navCtrl: NavController,
        private viewCtrl: ViewController,
        private zone: NgZone,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private authService: MarsAuthService,
        private globals: AppGlobals,
        private userInformationPages: AppUserPages,
        private interactionService: MarsInteractionService,
        private pictureService: MarsPictureService) {
        this.navigationService = new MarsNavigationService(this.app);
        this.navigationService.setNavCtrl(this.navCtrl);
        this.translations = this.locales.load();
    }

    ionViewWillEnter() {
        this.initialize();

    }

    ionViewDidEnter() {
        setTimeout(() => { this.changeDetector.detectChanges(); }, 200);
    }

    initialize() {
        this.pet = this.viewCtrl.data || this.pet;
        this.pet.status = this.viewCtrl.data && this.viewCtrl.data.status ? this.viewCtrl.data.status : this.pet.status;
        this.pet.pictures = this.pet.pictures || [];
        this.pet.mgWeight = this.pet.mgWeight || 0;
        this.pet.monthsAge = this.pet.monthsAge || 0;
        this.pet.address = this.pet.address || {};
        this.viewTitle = this.pet.status == "lost" ? this.translations.lost_pet : this.translations.pet;
        this.changeDetector.detectChanges();
    }

    getPetPicture() {
        let userHasAPicture = this.petHasAPicture();
        let latestPicture = userHasAPicture ? this.pet.pictures[this.pet.pictures.length - 1] : "";
        let pictureUrl = userHasAPicture ? this.pictureService.getPictureUrlFor({ _id: latestPicture }) : undefined;
        return userHasAPicture ? `background-image: url(${pictureUrl})` : "background-color: #efefef";
    }

    petHasAPicture() {
        return this.pet && this.pet.pictures && this.pet.pictures.length > 0;
    }

    async addPicture($event) {
        let file = $event.target.files[0];
        if (!file) return;
        this.changingPicture = true;
        let rawURL = URL.createObjectURL(file);
        file = await this.pictureService.properlyRotate(file) as any;
        /* let dimensions = (await this.getPictureDimensions(rawURL) as any); */
        let mustResize = file.size > 800000;
        let scaled = mustResize ? (await this.pictureService.scale(file, 0.2)) : file;
        let picture = { sent: (new Date()).toISOString(), src: rawURL, file: scaled, loading: true };
        this.pictureService.save((picture.file as any), { sent: picture.sent }, (error, saved) => {
            setTimeout(() => (this.changingPicture = false), 2000);
            try {
                if (error) {
                    throw new Error("Error!");
                } else {
                    saved = saved && typeof saved == "string" ? JSON.parse(saved)._id : saved._id;
                    this.pet.pictures.push(saved);
                }
            } catch (e) {

                this.interactionService.alert(this.translations.server_failure);
            } finally {
                this.changeDetector.detectChanges();
            }
        });
    }

    numbersOnly(str: string) {
        return str.replace(/\D/g, '');
    }

    getPictureDimensions(url) {
        return new Promise((resolve, reject) => {
            let image = new Image;
            image.onload = function () {
                resolve(image);
            };
            image.src = url;
        });
    }

    async removePicture(index) {
        let picture = this.pet.pictures.splice(index, 1)[0];
        let previous = index > 0 ? (index - 1) : 0;
        if (picture) {
            try { await Backend.removePicture({ id: picture._id, xAccessToken: MarsAuthService.getMarsToken() }); }
            catch (e) { }
            finally {
                this.changeDetector.detectChanges();
            }
        }
    }

    onAddressSelected(address) {
        address.complement = this.pet.address.complement || "";
        this.pet.address = address;
        this.changeDetector.detectChanges();
    }

    async save() {
        this.dismiss(this.pet);
    }

    close() {
        this.dismiss();
    }

    dismiss(pet?: any) {
        this.viewCtrl.dismiss(pet);
    }
}


